<template>
<div class="header">
    <Alert v-bind:show="show" @showfn1="showfn" v-if="show">xiashi</Alert>
    <el-row> 
        <el-col :span="8">
            <div class="grid-content bg-purple-light">
              <span>输出文件夹:</span>
                  <el-input v-model="input1" placeholder="请输入内容" style="width:300px; "></el-input>
            </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content bg-purple-light">
            <span>基础包名:</span>
            <el-input v-model="input2" placeholder="请输入内容" style="width:300px"></el-input>
          </div>
        </el-col>
      <el-col :span="8">
        <div class="grid-content bg-purple-light">
          <el-checkbox label="履盖现有的输出类"></el-checkbox>
        </div>
      </el-col>
    </el-row>
</div>
</template>

<script>
import Alert from '@/components/alert.vue'
export default {
    name:'header',
        data() {
      return {
        show: false,
        input1: '',
        input2: '',
        checkList: [],
      };
    },
    methods: {
        onClick() {
            this.show = true;
        },
        showfn(bool){
            this.show = bool
        }
    },
    components:{
        Alert
    }
  };

</script>

<style>
.header{
    height: 120px!important;
}
.header .el-row{
    line-height: 60px;
    height: 60px;
}
  /* /* .header{
     display: flex;
     flex-direction: row
  } */
   
</style>



