<template>
  <div class="main">
    <el-row  style="margin: 5px">
      <el-col :span="6">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>当前库所有表</span>
          </div>
          <el-table
            :data="tables"
            border
            highlight-current-row
            @current-change="handleCurrentChange"
            height="600"
            style="width: 100%">
            <el-table-column
              fixed
              prop="tableName"
              label="表名"
              width="180">
            </el-table-column>
            <el-table-column
              prop="common"
              label="名称"
            >
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
      <el-col :span="18">
        <el-row>
          <el-col :span="24">
              <el-form :inline="true" :model="generateSetting" label-position="left" size="mini" label-width="100px" class="setting-form" style="padding-top: 15px">
                <el-form-item label="输出文件夹" style="alignment: left">
                  <el-input v-model="generateSetting.outDir" placeholder="输出文件夹" style="width:400px"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-checkbox v-model="generateSetting.overwrite" label="履盖现有的输出类"></el-checkbox>
                </el-form-item>
              </el-form>
          </el-col>
        </el-row>
        <el-tabs v-model="activeTab" type="border-card" @tab-click="tabClick" style="margin-left: 5px">
          <el-tab-pane label="实体类管理 (DO)" name="entity">
            <EntityPanel v-if="selectedTable !== null  && activeTab == 'entity'" :selectedTable="selectedTable" @generated="generatedHandle" :selectedTableEntity="selectedTableEntity" :generateSetting="generateSetting"></EntityPanel>
          </el-tab-pane>
          <el-tab-pane label="Mapper 管理(Mapper)" name="mapper">
            <div v-if="selectedTable !== null && activeTab == 'mapper'">
              <el-form label-position="right" size="mini" :model="selectedTable">
                <div class="demo-input-suffix">
                  <span class="label">输出的类名</span>
                  <el-input
                    placeholder="输出包名"
                    size="mini"
                    v-model="selectedTable.mapperPackageName">
                  </el-input>
                  <el-input
                    placeholder="实体类名"
                    size="mini"
                    v-model="selectedTable.mapperClzName">
                  </el-input>
                </div>
                <el-input size="mini" placeholder="已经生成的 Mapper 类" :value="selectedTableEntity.beanClzName" :disabled="true"><template slot="prepend">已经生成的实体类</template></el-input>
                <el-form-item style="margin-top: 5px">
                  <el-button type="primary" @click="generateSubmit">立即创建 Mapper</el-button>
                </el-form-item>
              </el-form>
              <el-card>
                <pre v-highlightjs :key="selectedEntityMapper.mapperClass" v-if="mapperShowAble"><code class="java" >{{selectedEntityMapper.mapperScript}}</code></pre>
              </el-card>
            </div>
          </el-tab-pane>
          <el-tab-pane label="Service 管理 (Service)" name="service">
            <div v-if="activeTab === 'service'">
              <el-form label-position="right" size="mini" :model="selectedTable">
                <div class="demo-input-suffix">
                  <span class="label">输出的类名</span>
                  <el-input
                    placeholder="输出包名"
                    size="mini"
                    v-model="selectedTable.servicePackageName">
                  </el-input>
                  <el-input
                    placeholder="服务类名"
                    size="mini"
                    v-model="selectedTable.serviceClzName">
                  </el-input>
                </div>
                <el-input size="mini" placeholder="已经生成的 Service 类" :value="selectedTableEntity.serviceClzName" :disabled="true"><template slot="prepend">已经生成的服务类</template></el-input>
                <el-form-item style="margin-top: 5px">
                  <el-button type="primary" @click="generateSubmit">立即创建 Service</el-button>
                </el-form-item>
              </el-form>
              <el-card>
                <pre v-highlightjs :key="selectedEntityService.serviceClass" v-if="serviceShowable"><code class="java" >{{selectedEntityService.serviceScript}}</code></pre>
              </el-card>
            </div>
          </el-tab-pane>
          <el-tab-pane label="Controller 管理" name="controller">Controller 管理</el-tab-pane>
          <el-tab-pane label="批量处理" name="batch">
            <BatchPanel ref="batchPanel" :generateSetting="generateSetting"></BatchPanel>
          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import {listTables, getSetting, generate, listEntities, listTableEntities, getMapper, getService} from '@/api/dal'
  import BatchPanel from '@/components/batchPanel';
  import EntityPanel from '@/components/entityPanel';
  export default {
    data() {
      return {
        activeTab:'entity',
        tables: [],
        selectedTable: null,
        selectedTableEntity: null,
        selectedEntityMapper: {
          mapperScript: '',
          mapperClass:'mapper',
          tableName: ''
        },
        selectedEntityService: {
          serviceScript: '',
          serviceClass:'service',
          tableName: ''
        },
        mapperShowAble: false,
        serviceShowable: false,
        selectedMapperScript: '',
        tableEntities: [],
        generateSetting: {
          outDir: '',
          outPackage: '',
          overwrite:true
        }
      }

    },
    methods: {
      handleCurrentChange(table) {
        this.selectedTable = table;
        if (this.activeTab === 'mapper') {
          this.updateMapper();
        }
        if (this.activeTab === 'service') {
          this.updateService();
        }

        let tableName = table.tableName;
        let that = this;
        that.selectedTableEntity ={};
        this.tableEntities.forEach(te => {
          if (te.table == tableName) {
            that.selectedTableEntity = te;
          }
        });
        //
        if (this.activeTab === 'batch') {
          this.$refs.batchPanel.tableSelected(table);
        }

      },
      generateSubmit: function (event) {
        generate(this.generateSetting, this.selectedTable, this.activeTab).then((resp) =>{
          this.$message({
            message: '操作成功',
            type: 'success'
          });
          listEntities().then(resp => {
            this.tableEntities = resp;
          });
        });
      },
      tabClick: function(tab, event) {
        //console.log(tab, event);
        if (this.activeTab == 'mapper') {
          this.updateMapper();
        }
        if (this.activeTab == 'service') {
          this.updateService();
        }

      },
      updateMapper: async function () {
        if (this.selectedTableEntity != null && this.selectedTableEntity.table === this.selectedEntityMapper.tableName) {
          //console.log(" Mapper 已经存在，将要返回!");
          //return;
        }
        this.mapperShowAble = false;
        let that = this;
        let resp = await getMapper(this.selectedTable);
        that.selectedEntityMapper = resp;
        this.mapperShowAble = true;
      },
      updateService: async function () {
        if (this.selectedTableEntity != null && this.selectedTableEntity.table === this.selectedEntityService.tableName) {
          //console.log(" Mapper 已经存在，将要返回!");
          //return;
        }
        this.serviceShowable = false;
        let that = this;
        let resp = await getService(this.selectedTable);
        that.selectedEntityService = resp;
        this.serviceShowable = true;
      },
      generatedHandle: function () {
        listEntities().then(resp => {
          this.tableEntities = resp;
        });
      }
    },
    mounted() {
      listEntities().then(resp => {
        this.tableEntities = resp;
      });
      listTableEntities().then((resp) => {
        this.tables = resp;
        console.log(resp);
      });
      getSetting().then((resp) => {
        this.generateSetting = resp;
        console.log(resp);
      });

    },
    components: {BatchPanel, EntityPanel}
  }
</script>

<style scoped>
  .demo-input-suffix {
    margin-bottom: 15px;
  }
  .demo-input-suffix .el-input {
    display: inline-block;
    width: 45%;
  }
  .demo-input-suffix .label {
    font-size: 14px;
  }
  .header {
    height: 120px !important;
  }

  .header {
    line-height: 60px;
    height: 60px;
  }

  .grid-content {
    background-color: #efefef;
    color: #333;
    text-align: center;
  }

  .right-item {
    max-width: 300px;
  }

  .top-bar {
    margin: 10px;
    clear: both;
    text-align: left;
  }

  .main {
    height: 100%;
    width: 100%;

  }

  .main > div {
    flex: 1;
    padding-left: 10px;
    padding-right: 10px;
  }

  .main .list-box {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 12px 30px;
    margin-top: 120px
  }

  .list-box > div {
    margin-right: 20px;
    width: 252px
  }
.setting-form {
  background-color: #efefef;
}
  .left-item {
    max-width: 310px;
  }

  .left-item, .center-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .center-item {
    min-width: 550px;
    left: 15px;
    margin-right: 15px;
  }

  /* .title-param{
      text-align: left;
      padding-left: 30px;
  } */
  .main .el-tag {
    display: block;
    margin-bottom: 15px;
    width: 130px;
  }

  .left-wraper {
    height: 100%;
    width: 100%;
  }

  .btn-wraper {
    display: flex;
  }

  .btn-wraper > button {
    flex: 1;
  }
  .hljs {
    font-family: Fira Mono,monospace;
    font-size: .9em;
    text-align: left;
  }
  .hljs {
    display: block;
    overflow-x: auto;
    padding: .5em;
    //background: #272822;
    //color: #ddd;
  }
</style>



