<template>
  <el-container class="wrap-content">
    <el-header>
      <TopMenu></TopMenu>
    </el-header>
    <el-main>
      <Main></Main>
    </el-main>
  </el-container>
</template>

<script>
import SideBar from '@/components/sideBar.vue'
import Header from '@/components/header.vue'
import TopMenu from '@/components/topMenu.vue'

import Main from './main.vue'
export default {
  components:{
    SideBar,
    Header,
    Main,
    TopMenu
  },
}
</script>

<style scoped>
.wrap-content{
  height: 100%;
}

  .el-aside {
    background-color: #337ab7;
    color: #333;
    text-align: center;
  }
  
  .el-main {
    color: #333;
    text-align: center;
  }
  
  body > .el-container {
    margin-bottom: 40px;
  }

</style>



