<template>
  <div>
    <!-- <el-button type="text" @click="dialogVisible = true">点击打开 Dialog</el-button> -->
    <el-dialog title="提示" :visible.sync="show" width="30%"  :before-close="handleClose"  close-on-click-modal="false">
      <span>这是一段信息</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "alert",
   props: {
    show: {
      type: Boolean,
    }
  },
    data() {
      return {
      
      };
    },
    methods:{
            cancle() {
            this.$emit('showfn1', false);
        },
            confirm() {
            this.$emit('showfn1', false);
        },
        
            handleClose() {
            this.$emit('showfn1', false);
        },
    }
};
</script>

