<template>
    <div>
      <div style="margin-bottom: 5px">
        <el-button-group>
          <el-button icon="el-icon-remove" @click="removeSelected()">取消所选的表</el-button>
          <el-button icon="el-icon-finished" @click="batchGeneratedSubmit()">批量生成</el-button>
        </el-button-group>
      </div>
      <el-table
        :data="tables"
        border
        highlight-current-row
        max-height="500"
        ref="tableList"
        style="width: 100%">
        <el-table-column
        type="selection"
        prop="tableName"
        width="55"/>
        <el-table-column
          fixed
          prop="tableName"
          label="表名"
          width="180">
        </el-table-column>
        <el-table-column
          prop="common"
          label="名称"
        >
        </el-table-column>
      </el-table>
    </div>
</template>

<script>
  import {batchGenerated} from '@/api/dal'
    export default {
        name: "batchPanel",
        props:{
          generateSetting: {type: Object}
        },
        data(){
          return {
            selectedTable:{},
            tables:[]
          }
        },
        methods: {
          tableSelected(table) {
            console.log(table);
            this.selectedTable = table;
            let index = this.tables.findIndex((value,index,arr)=>{return value.tableName === table.tableName});
            if (index === -1 ){
              this.tables.push(table);
            }
          },
          removeSelected() {
            let selection = this.$refs.tableList.selection;
            let that = this;
            let skipTables= that.tables;
            selection.forEach((table) => {
              //console.log(table);
              let index = skipTables.findIndex((value,index,arr)=>{return value.tableName === table.tableName});
              if (index > -1) {
                skipTables.splice(index,1);
              }
            });
          },
          batchGeneratedSubmit() {
            let setting = this.generateSetting;
            batchGenerated(setting, this.tables).then((resp) =>{
              this.$message({
                message: '操作成功',
                type: 'success'
              });
            });
          }
        },
        mounted() {
          //console.log(this.generateSetting);
          // window.Vue.$on('tableSelectedEvent', (selectedTable) => {
          //     this.tableSelected(selectedTable);
          // });
        }
    }
</script>

<style scoped>

</style>



