import request from '@/utils/request'
//获取所有的表
export function listTables() {
  return request({
    url: '/dal/tables',
    method: 'get'
  })
}
export function listTableEntities() {
  return request({
    url: '/dal/table/entities',
    method: 'get'
  })
}
export function getSetting() {
  return request({
    url: '/dal/generate/setting',
    method: 'get'
  })
}

export function listEntities() {
  return request({
    url: '/dal/list/entities',
    method: 'get'
  })
}

export function generate(setting,tableSchema,type) {
  let generateSetting = {setting,tableSchema};
  return request({
    url:'/dal/generated?type='+type,
    method:'post',
    data: generateSetting
  });
}
export function getMapper(tableSchema) {
  let generateSetting = tableSchema;
  console.log(generateSetting);
  return request({
    url:'/dal/get/mapper',
    method:'post',
    data: generateSetting
  });
}
export function getService(tableSchema) {
  let generateSetting = tableSchema;
  console.log(generateSetting);
  return request({
    url:'/dal/get/service',
    method:'post',
    data: generateSetting
  });
}

export function batchGenerated(setting, tableSchemas) {
  let generateSetting = {setting,tableSchemas};
  return request({
    url:'/dal/generated/batch',
    method:'post',
    data: generateSetting
  });
}



