import Vue from 'vue'
import Router from 'vue-router'
import Login from '../pages/login'
import Home from '@/pages/homepage'
Vue.use(Router)

export default new Router({
  routes: [
    {
      name: 'home',
      path: '/',
      component: Home
    },{
    name: 'home',
    path: '/home',
    component: Home
  }]
})



