<template>
    <div>
      <div v-if ="selectedTable == null">
        <el-alert
          title="还没有选择要操作的表"
          type="success"
          effect="dark">
        </el-alert>
      </div>
      <div v-if="selectedTable !== null">
        <el-form label-position="right" size="mini" :model="selectedTable">
          <div class="demo-input-suffix">
            <span class="label">输出的类名</span>
            <el-input
              placeholder="输出包名"
              size="mini"
              v-model="selectedTable.entityPackageName">
            </el-input>
            <el-input
              placeholder="实体类名"
              size="mini"
              v-model="selectedTable.entityName">
            </el-input>
          </div>
          <el-input size="mini" placeholder="已经生成的实体类" :value="selectedTableEntity.beanClzName" :disabled="true"><template slot="prepend">已经生成的实体类</template></el-input>
          <el-form-item style="margin-top: 5px">
            <el-button type="primary" @click="generateSubmit">立即创建实体类</el-button>
          </el-form-item>
        </el-form>
        <el-table border
                  height="600"
                  :data="selectedTable.columns"
                  style="width: 100%"
        >
          <el-table-column
            label="字段"
            property="columnName"
            width="150">
          </el-table-column>
          <el-table-column
            property="common"
            label="名称"
            width="120">
          </el-table-column>
          <el-table-column
            property="typeName"
            label="类型"
            width="120">
          </el-table-column>
          <el-table-column
            property="length"
            label="长度"
            width="120">
          </el-table-column>
          <el-table-column
            property="propertyName"
            label="实体类属性名称"
          >
          </el-table-column>
          <el-table-column label="添加属性注释">
            <template slot-scope="scope">
              <el-select size="mini" v-model="tableAnnotations[scope.row.columnName]" placeholder="请选择注释">
                <el-option label="逻辑删除字段" value="logicDelteColumn"></el-option>
                <el-option label="乐观锁字段" value="revisionColumn"></el-option>
                <el-option label="更新日期时间字段" value="updatedTimeColumn"></el-option>
                <el-option label="创建日期时间字段" value="createdTimeColumn"></el-option>
              </el-select>
            </template>
          </el-table-column>

          <el-table-column
            property="columnClz"
            label="实体类属性类型"
          >
          </el-table-column>

        </el-table>
      </div>
    </div>
</template>

<script>
  import {listTables, getSetting, generate, listEntities, listTableEntities, getMapper, getService} from '@/api/dal'
    export default {
      name: "entityPanel",
      props:{
        selectedTable: {type: Object},
        selectedTableEntity: {type: Object},
        generateSetting: {type: Object}
      },
      data(){
        return {
          tableAnnotations:{}
        }
      },
      methods: {
        generateSubmit: function (event) {
          let self = this;
          Object.keys(this.tableAnnotations).forEach((key) =>{
            let value = this.tableAnnotations[key];
            self.selectedTable[value] = key;
          });
          let that = this;
          generate(this.generateSetting, self.selectedTable, 'entity').then((resp) =>{
            this.$message({
              message: '操作成功',
              type: 'success'
            });
            that.$emit('generated');
          });
        }
      },
      mounted() {
        this.tableAnnotations[this.selectedTable.logicDelteColumn] = 'logicDelteColumn';
        this.tableAnnotations[this.selectedTable.revisionColumn] = 'revisionColumn';
        this.tableAnnotations[this.selectedTable.createdTimeColumn] = 'createdTimeColumn';
        this.tableAnnotations[this.selectedTable.updatedTimeColumn] = 'updatedTimeColumn';
      }
    }
</script>

<style scoped>
  .demo-input-suffix {
    margin-bottom: 15px;
  }
  .demo-input-suffix .el-input {
    display: inline-block;
    width: 45%;
  }
  .demo-input-suffix .label {
    font-size: 14px;
  }
</style>



