<template>
  <div class="wrap">
    <div class="logo"> </div>
    <el-form class="form" label-position="top" label-width="80px" :model="formdata">
      <h3>用户登录</h3>
      <el-form-item label="用户名">
        <el-input v-model="formdata.username"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input type="password" v-model="formdata.password"></el-input>
      </el-form-item>
      <el-button @click="handleLogin()" type="primary" class="btn" >登录</el-button>
    </el-form>
  </div>
</template>

<script>
export default {
    data() {
    return {
      formdata: {
        username: "",
        password: ""
      }
    }
  },
  methods: {
    // 登录请求
     handleLogin(){
       if(localStorage.getItem('token')){
           this.$router.push({
            name:'home'
          });
       }else{
        if(this.formdata.username.trim() === 'admin'&& this.formdata.password.trim() === "admin"){
          this.$message.success("登陆成功")
          localStorage.setItem("token","sdsdfsdfsdf")
          this.$router.push({
            name:'home'
          });
       }else{
          this.$message.error('账号或密码错误')
       }
       }
     
         
  },

}
}
</script>

<style>
.wrap{
  height: 100%;
  background-color: #324152;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wrap .form{
  background-color: #fff;
  border-radius: 10px;
  width: 400px;
  padding: 30px;
}
.wrap .form .btn {
  width: 100%;
}  
</style>



